.product{
    display: flex;
    border-bottom:1px solid lightgray;
    margin-bottom: 5px;
    margin-right: 10px;
    padding-bottom: 5px;
    padding: 50px;

}
.product-name{
    color: blue;
    font-weight: 400;
}
.main-button{
    background-color: rgb(238, 190, 67);
    width: 175px;
    height: 25px;
    border: 1px solid gray;
    border-radius: 5px;
    cursor: pointer;
}
.products-names{
    margin-left: 15px;
}